<template>
	<div class="app-container">
		<el-alert title="注意:处理过程约耗时0~5分钟，请稍后查询" show-icon type="warning">
		</el-alert>
		<inputs-header :options="options" :isAdmin="true" @change='changeCompany' @btnClick="checkIccid" class="clearfix"></inputs-header>
		<el-table :data="tableData" v-loading="isLoad" border style="width: 100%">
			<el-table-column align="center" prop="number" label="接入号码" width="140">
			</el-table-column>
			<el-table-column align="center" prop="iccid" label="ICCID">
			</el-table-column>
			<el-table-column align="center" prop="orderId" label="订单号">
			</el-table-column>
			<el-table-column align="center" prop="time" label="交易时间" width="120">
			</el-table-column>
			<el-table-column align="center" prop="content" width="200" label="订单内容" :formatter="orderContent">
			</el-table-column>
			<!-- <el-table-column align="center" prop="content" width="100" label="充值状态" :formatter="failureStatus">
				</el-table-column> -->
			<el-table-column align="center" prop="price" label="金额(元)" width="100">
			</el-table-column>
			<el-table-column align="center" label="操作" width="130">
				<template scope="scope">
					<el-button type="button" size="small">重新订购</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import InputsHeader from 'components/InputsHeader/index';
import { backCN } from 'utils/index'
import {
	giveManufacturerAuthority,
	getListManufacturerGeneral
} from 'api/admin/usual';
import {
	getListFailPayOrderByMid,
	listFailPayOrderByIccid
} from 'api/admin/orderFailInfo';
export default {
	data() {
		return {
			options: [],
			value: '',
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			iccidInput: '',
			iccidArr: [],
			isLoad: true,
			isMid: true
		}
	},
	watch: {},
	mounted() {
		this.initHeader()
	},
	methods: {
		initHeader() {
			this.isLoad = true
			getListManufacturerGeneral().then(res => {
				
				res.msg === 0 ? this.options = res.mList : this.$message.error(backCN(res.code));
				res.msg != 0 ? this.isLoad = false : ''
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			})
		},
		checkIccid(iccid) {
			if (iccid) {
				this.iccidInput = iccid;
				this.getListFailPayOrderByIccid(iccid,  1, 10 );
			} else {
				this.listFailPayOrderByMid(1, 10);
			}
		},
		changeCompany(mid) {
			this.isLoad = true;
			giveManufacturerAuthority({mid}).then(res => {
					if(res.msg===0){
						if(this.iccidInput.length>0){
                           this.getListFailPayOrderByIccid(this.iccidInput,1,10)
						}else{
							this.ListFailPayOrderByMid(1, 10)
						}
					}else{
						this.$message.error(backCN(res.code))
					}
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		pageChange(page) {
			if (this.iccidInput.length > 0) {
				this.getListFailPayOrderByIccid(this.iccidInput, page,10);
			} else {
				this.ListFailPayOrderByMid(page,10)
			}
		},
		orderContent(val) {
			return val.content.replace(/\（数据\）/g, '').replace(/非定向/g, '').split('（')[0];
		},
		ListFailPayOrderByMid(page, num) {
			this.isLoad = true
			getListFailPayOrderByMid({ page, num })
				.then(res => {
					this.isLoad = false;
					if (res.msg == 0) {
						this.tableData = res.mList;
						this.tableCount = res.count;
						page === 1 ? this.tableCount = res.count : ''
					} else {
						this.tableData = [];
						this.tableCount = 0;
					}
					(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		getListFailPayOrderByIccid(iccid, page, num) {
			this.isLoad = true
			listFailPayOrderByIccid({ iccid, page, num }).then(res => {
				this.isLoad = false;
				if (res.msg == 0) {
					this.tableData = res.mList;
					this.tableCount = res.count;
					page === 1 ? this.tableCount = res.count : ''
				} else {
					this.tableData = [];
					this.tableCount = 0;
				}
				(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});

		}
	},

	components: {
		InputsHeader
	}
};
</script>

<style lang="scss" scoped>
.app-container {
	background: #f5f5f5;
}
</style>


